import React from "react";
import { url } from '../../config/config';
import './home.css';
import { useEffect, useState } from 'react';
import {
  EmailShareButton,
  EmailIcon,
  FacebookShareButton,
  FacebookIcon,
  TwitterShareButton,
  TwitterIcon,
  WhatsappShareButton,
  WhatsappIcon,
  LinkedinShareButton,
  LinkedinIcon,
  RedditShareButton,
  RedditIcon,
  TelegramShareButton,
  TelegramIcon
} from "react-share";
import IndiaFlag from '../../assets/images/india-flag.svg';
import ChinaFlag from '../../assets/images/china-flag.svg';
import SomaliaFlag from '../../assets/images/somalia-flag.svg';


export default function Home(props) {
  const [isFindProduct, setFindProduct] = useState(false);
  const [isInput, setInput] = useState(false);
  const [country, setCountry] = useState('');
  const [selectedBrand, setBrand] = useState('');
  const [selectedProduct, setProduct] = useState('');
  const [brandFilter, setBrandFilter] = useState([]);
  const [brands, setBrandsIndex] = useState([]);
  const [urlToShare] = useState('https://buyinbharat.com')

  useEffect(() => {
    if (brands.length <= 0) {
      fetch(url.baseUrl + url.brandsArray).then(
        res => res.json()
      ).then((data) => {
        setBrandsIndex(data);
      })
    }
    let e = {};
    e = {target:{}}
    e['target']['value'] = props.search;
    if(!isFindProduct){
      setFindProduct(true);
      findProduct(e);
    }
    if(props && 'search' in props && props.search){
      selectBrand(e);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <div className='container-fluid'>
      <div className='row searchbar'>
        <div className="col-sm-12 col-md-6">
          <div className="row">
            <span className="float-right">किस देश का उत्पाद है?</span>
          </div>
          <div className='row'>
            <span className="float-right">Which country's brand is?</span>
          </div>
        </div>
        <div className="col-sm-12 col-md-6">
          {props && 'search' in props && props.search ?
          <button onClick={gotoHome} className='btn btn-primary'>Search</button>
          :
          <input type="text" placeholder='खोजें/Search' className="search_input" value={selectedProduct && (selectedProduct[0].toUpperCase() + selectedProduct.slice(1,))} name="search" onChange={e => findProduct(e)} autoComplete="off" autoFocus="" />
        }</div>
      </div>
      {isInput ? <div>
        <ul className='list'>
          {
            brandFilter.length > 0 ? <div>
              {brandFilter.map((brand, i) => {
                return (
                  <li key={i}>
                    <button value={brand} onClick={(e) => selectBrand(e)}>
                      {`${brand.slice(0, 1).toUpperCase()}${brand.slice(1,)}`}
                    </button>
                  </li>
                )
              }
              )}
            </div> :
              ''
          }
        </ul>
      </div> : ''}
      <div className={`row main-content ${country ? 'active' : 'inactive'}`}>
        <div className="col-sm-12 col-md-3 offset-md-2">
          <div className="card">
            {
              country === 'india' ?
                <img className="card-img-top" src={IndiaFlag} alt="IndiaFlag" />
                :
                (country === 'china' ?
                  <img className="card-img-top" src={ChinaFlag} alt="ChinaFlag" />
                  :
                  <img className="card-img-top" src={SomaliaFlag} alt="WorldFlag" />
                )
            }
            <div className="card-body">
              <p className="card-text">{`${country === 'india' ? 'भारत' : (country === 'china' ? 'चीन' : 'अन्य देश')} का उत्पाद`}</p>
              <p className="card-text">{`Brand of ${country.toUpperCase()}`}</p>
            </div>
          </div>
        </div>
        <div className="col-sm-12 col-md-5">
          <div className="card">
            <div className="card-body">
              <p className="card-text"><b>Company</b></p>
              {selectedBrand.brand && <p className="card-text">{`${selectedBrand.brand.slice(0, 1).toUpperCase()}${selectedBrand.brand.slice(1,)}`}</p>}
              <p className="card-text"><b>श्रेणी/Category</b></p>
              <p className="card-text">{`${selectedBrand.groups}`}</p>
              <p className="card-text"><b>संदर्भ/References</b></p>
              <p className="card-body">
                {selectedBrand && 'references' in selectedBrand && selectedBrand.references.length > 0 ?
                  <ul>{selectedBrand.references.map(ref => {
                    return (<li><a target="_blank" rel="noopener noreferrer" href={ref}>{ref}</a></li>)
                  })}</ul>
                  : ''}
              </p>
            </div>
          </div>
        </div>
      </div>
      <div className="container">
        <div className="btn-group-vertical" role="group" aria-label="Button group with nested dropdown">
          <button type="button" className="btn btn-secondary">
            <a className="footer-link" href="https://docs.google.com/forms/d/e/1FAIpQLSd4NnHXtvv7DpY93-6ZcwUk1tkvL78eT6tSyDNDdT3lY2j0hw/viewform" target="_blank" rel="noopener noreferrer">किसी भी गलत जानकारी की सूचना दें (Report any incorrect data)</a>
          </button>
          <button type="button" className="btn btn-secondary">
            <a className="footer-link" href="https://docs.google.com/forms/d/e/1FAIpQLSfOLjHq0Crnm_6izV9N2WNO9NP9DwYttsZKg9Iqa3j-a5LYOA/viewform" target="_blank" rel="noopener noreferrer">कंपनी / उत्पाद जोड़ने का अनुरोध (Company/Product add request)</a>
          </button>
          <button type="button" className="btn btn-secondary">
            <a className="footer-link" href="https://docs.google.com/forms/d/e/1FAIpQLSfckly2X5CcO-1cEKMBVWDJtJZvjrPW3ivaBb3zvHCCm3bbmg/viewform" target="_blank" rel="noopener noreferrer">सुझाव या शिकायत दर्ज करें (Suggestions or Complaints)</a>
          </button>
        </div>
      </div>
      <br />
      <TwitterShareButton url={urlToShare} className='share-button'>
        <TwitterIcon size={32} />
      </TwitterShareButton>
      <EmailShareButton url={urlToShare} className='share-button'>
        <EmailIcon size={32} />
      </EmailShareButton>
      <WhatsappShareButton url={urlToShare} className='share-button'>
        <WhatsappIcon size={32} />
      </WhatsappShareButton>
      <FacebookShareButton url={urlToShare} className='share-button' >
        <FacebookIcon size={32} />
      </FacebookShareButton>
      <LinkedinShareButton url={urlToShare} className='share-button'>
        <LinkedinIcon size={32} />
      </LinkedinShareButton>
      <RedditShareButton url={urlToShare} className='share-button'>
        <RedditIcon size={32} />
      </RedditShareButton>
      <TelegramShareButton url={urlToShare} className='share-button'>
        <TelegramIcon size={32} />
      </TelegramShareButton>
    </div>
  );

  function gotoHome(){
    setInput(false);
    setProduct('');
    setBrand('');
    setCountry('');
    props.routerProps.history.replace('/');

  }

  function selectBrand(e) {
    setProduct(e.target.value);
    fetch(url.baseUrl + "data-store/brand-" + e.target.value.toLowerCase() + ".json").then(
      res => res.json()
    ).then((data) => {
      if (data.alias !== "") {
        fetch(url.baseUrl + "data-store/brand-" + data.alias.toLowerCase() + ".json").then(
          res => res.json()
        ).then(
          data => {
            setBrand(data);
            setCountry(data.country);
          }
        )
      } else {
        setBrand(data);
        setCountry(data.country);
      }
    })
    setInput(false);
  }

  function findProduct(e) {
    let brandFilter = [];
    let text = '';
    if (e.target.value) {
      text = e.target.value.toLowerCase();
      setProduct(text);
      setInput(true);
      setCountry('');
    } else {
      setInput(false);
      setProduct('');
    }
    
    for (let brand of brands) {
      if (text === brand.substr(0, text.length)) {
        brandFilter.push(brand);
      }
    }
    setBrandFilter(brandFilter);
  }

}