import React from "react";
import Home from '../Pages/Home/home';
import NoMatch from '../Pages/pageNotFound/pageNotFound';
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";

function Routes() {
  return (
    <Router>
      <Switch>
        <Route exact path="/" component={Home} />
        <Route path="/brands/:value" render = {routerProps => renderBrands(routerProps)} /> 
        <Route path="*" component={NoMatch} />
      </Switch>
    </Router>
  );

  function renderBrands (routerProps) {
    if(routerProps.match.params.value){
      return <Home routerProps={routerProps} search={routerProps.match.params.value} />
    }
  }
}

export default Routes;

